<template>
  <div class="home">
    <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
    <!-- <HelloWorld msg='test'/> -->
    <TypingEffect v-on:loadFinish="loadFinish" v-bind:style="{ display:isLoading?'flex;':'none' }" />
    <div v-bind:style="{ display:isLoading?'none':'flex' }"><VideoPlayer/></div>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'
import TypingEffect from '@/components/TypingEffect.vue'
import VideoPlayer from '@/components/VideoPlayer.vue'

export default {
  name: 'home',
  data() {
    return {
      isLoading:true
    };
  },
  components: {
    HelloWorld,
    TypingEffect,
    VideoPlayer,
  },
  methods:{
    loadFinish(){
      this.isLoading = false;
    }
  }
}
</script>
<style lang="scss">
.home {
  position: absolute;
  display: block;
  align-items: center;
  width: 1080px;
  height: 1920px;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  padding: 0px;
  margin: 0px;
  justify-content: center;
}
